import { NgModule,NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { DailyStoreLogReportRoutingModule } from './daily-store-log-report-routing.module';
import { DailyStoreLogReportComponent } from './daily-store-log-report.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    DailyStoreLogReportRoutingModule
  ],
  declarations: [
    DailyStoreLogReportComponent
  ],
  schemas:[
    NO_ERRORS_SCHEMA
  ]
})
export class DailyStoreLogReportModule { }



