import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DailyStoreLogReportComponent } from './daily-store-log-report.component';

const routes: Routes = [
    {
        path: '', component: DailyStoreLogReportComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class DailyStoreLogReportRoutingModule { }



